import { baseUrl } from '../config'
const axios = require('axios')

let headers = {
  'Accept': 'application/json',
  'Content-Type': 'application/json',
  'Access-Control-Max-Age': 86400
};

export const getRequest = async (route, token) => {
  try {
    if (token) headers['X-Access-Token'] = token;
    const response = await axios.get(baseUrl + route, {
      headers: headers
    });
    return response.data;
  }
  catch(error) {
    console.log(error);
    return { error: 'get_error', message: error.toString() }
  }
};

export const postRequest = async (route, body, token) => {
  try {
    if (token) headers['X-Access-Token'] = token;
    const response = await axios.post(baseUrl + route, body, {
      headers: headers
    });
    return response.data;
  }
  catch(error) {
    console.log(error);
    return { error: 'post_error', message: error.toString() }
  }
};



