import React from 'react';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import { Link } from "react-router-dom";

import { compose } from "redux";
import { connect } from "react-redux";
import { setAuth } from "../actions/auth";

import { postRequest } from '../lib/requests';

const mui_styles = theme => ({
  button: {
    margin: theme.spacing.unit,
  },
  input: {
    display: 'none',
  },
});

class Login extends React.Component {

  state = {
    loading: false,
    error: false,
    email: 'isidrogilabert@gmail.com',
    password: 'Androllo6502$',
    token: ''
  };

  onChange = (event) => {
    const { target: { name, value } } = event;
    this.setState(() => ({ [name]: value }))
    console.log([name], value);
  }

  canLogIn = () => {
    const { email, password } = this.state;
    return !(email && password);
  };

  onLogIn = async () => {
    console.log("onLogin");
    this.setState({loading: true, error: false});
    try {
      const { email, password } = this.state;
      const response = await postRequest('/users/auth', {email: email, password: password});
      console.log(response);
      this.setState({error: response.error, errorMessage: response.message, loading: false});
      if (!this.state.error) {
        this.props.setAuth(response.token);
        //this.setState( { token: response.token });
      }
      else {
        console.log("Received error");
      }
    } catch (error) {
      this.setState({loading: false, error: true});
    }
  };

  render() {
    const { error, errorMessage } = this.state;
    const { classes, auth } = this.props;
    if (auth.token) {
      return(
        <div>
          Logged In!
        </div>
      );
    }
    else
    
    return (
      <div style={styles.container}>
        <h2>Login</h2>
        <div>
          <TextField
            style={styles.textInput}
            placeholder={'Email'}
            autoCapitalize={'none'}
            onChange={this.onChange}
            value={this.state.email}
            name={'email'}
          />
        </div>
        <div>
          <TextField
            style={styles.textInput}
            placeholder={'Password'}
            type={'password'}
            onChange={this.onChange}
            value={this.state.password}
            name={'password'}
          />
        </div>
        <div>
          <Button
            variant="raised" color="primary" className={classes.button}
            disabled={this.canLogIn() || this.state.loading}
            onClick={this.onLogIn}
          >
            Log in
          </Button>
        </div>
        <div>
          {error && <p style={{textAlign: 'left', color: "#ff0000"}}>{errorMessage}</p>}
        </div>
        <div>
          <Link to="/new">Signup</Link>
        </div>
      </div>
    );
  }
}

const styles = {
  container: {
    flex: 1,
    justifyContent: 'center'
  },
  textInputView: {
    borderWidth: 1,
    borderColor: '#efefef',
    borderRadius: 3,
    marginVertical: 3,
    marginHorizontal: "3%",
  },
  textInput: {
    fontSize: 12,
    textAlign: 'center',
    margin: 10,
    color: '#999',
    width: '300px'
  },
};

function mapStateToProps(state) {
  return {
    auth: state.get('auth')
  };
}

const actions = {
  setAuth
};

export default compose(
  withStyles(mui_styles),
  connect(mapStateToProps, actions)
)(Login);



