import React from 'react';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import { cmsUrl } from '../config'

import { compose } from "redux";
import { connect } from "react-redux";
import { setAuth } from "../actions/auth";

import { postRequest } from '../lib/requests';

const mui_styles = theme => ({
  button: {
    margin: theme.spacing.unit,
  },
  input: {
    display: 'none',
  },
});

class New extends React.Component {

  state = {
    loading: false,
    error: '',
    email: 'isidrogilabert@gmail.com',
  };

  onChange = (event) => {
    const { target: { name, value } } = event;
    this.setState(() => ({ [name]: value }))
    console.log([name], value);
  }

  canLogIn = () => {
    const { email } = this.state;
    return !(email);
  };

  onLogIn = async () => {
    this.setState({loading: true, error: false});
    try {
      const { email } = this.state;
      const response = await postRequest('/create-user', {email: email, url: cmsUrl});
      this.setState({error: response.error, errorMessage: response.message, loading: false});
      if (!this.state.error) {
        this.setState({loading: false, error: false});
      }
      else {
        console.log("Received error");
      }
    } catch (error) {
      this.setState({loading: false, error: true});
    }
  };

  render() {
    const { error, errorMessage } = this.state;
    const { classes } = this.props;
    return (
      <div style={styles.container}>
        <h2>Signup</h2>
        <div>
          <TextField
            style={styles.textInput}
            placeholder={'Email'}
            autoCapitalize={'none'}
            onChange={this.onChange}
            value={this.state.email}
            name={'email'}
          />
        </div>
        <div>
          <Button
            variant="raised" color="primary" className={classes.button}
            disabled={this.canLogIn() || this.state.loading}
            onClick={this.onLogIn}
          >
            Submit
          </Button>
        </div>
        <div>
          {error && <p style={{textAlign: 'left', color: "#ff0000"}}>{errorMessage}</p>}
        </div>
      </div>
    );
  }
}

const styles = {
  container: {
    flex: 1,
    justifyContent: 'center'
  },
  textInput: {
    fontSize: 12,
    textAlign: 'center',
    margin: 10,
    color: '#999',
    width: '300px'
  },
};

function mapStateToProps(state) {
  return {
    auth: state.get('auth')
  };
}

const actions = {
  setAuth
};

export default compose(
  withStyles(mui_styles),
  connect(mapStateToProps, actions)
)(New);



