import {
  SET_AUTH, 
} from "../constants/auth";

const initialState = {
  token: null,
  error: '',
};

export default function authReducer(state = initialState, action) {
  console.log("reducer auth");
  console.log(action);
  console.log(state);
  switch(action.type) {
    case SET_AUTH:
      return {
        ...state,
        token: action.token,
      };
    default:
      return state;
  }
}



