import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import { compose } from "redux";
import { connect } from "react-redux";
import CircularProgress from '@material-ui/core/CircularProgress';

import { getRequest } from '../lib/requests';

const mui_styles = theme => ({
  button: {
    margin: theme.spacing.unit,
  },
  input: {
    display: 'none',
  },
});

class Me extends React.Component {

  state = {
    loading: false,
    loaded: false,
    error: false,
  };

  async load(props) {
    if (!props.auth.token) {
      console.log("No token provided to me");
      return
    }
    else {
      this.setState({
        loading: true
      });
      const response = await getRequest('/users/me', props.auth.token);
      this.setState({
        loaded: true,
        email: response.HashKey,
        coins: response.coins
      })
    }
  }

  async componentWillMount() {
    await this.load(this.props);
  }

  async componentWillReceiveProps(props) {
    await this.load(props);
  }

  render() {
    const { error, errorMessage } = this.state;
    const { classes } = this.props;

    if (!this.state.loading && !this.state.loaded) {
      return (
        <div style={styles.container}>
          <h2>My Profile</h2>
          <p>Not loged in</p>
        </div>
      )
    }

    if (this.state.loading && !this.state.loaded) {
      return (
        <div style={styles.container}>
          <h2>My Profile</h2>
          <CircularProgress className={classes.progress} />
        </div>
      )
    }

    return (
      <div style={styles.container}>
        <h2>My Profile</h2>
        { this.state.loading && !this.state.loaded ? 
          <CircularProgress className={classes.progress} />
          :
          <div>
            <div>
              <p style={styles.textInput}>
                Email: {this.state.email}
              </p>
            </div>
            <div>
              <p style={styles.textInput}>
                Coins: {this.state.coins}
              </p>
            </div>
          </div>
        }
        <div>
          {error && <p style={{textAlign: 'left', color: "#ff0000"}}>{errorMessage}</p>}
        </div>
      </div>
    );
  }
}

const styles = {
  container: {
    flex: 1,
    justifyContent: 'center'
  },
  textInputView: {
    borderWidth: 1,
    borderColor: '#efefef',
    borderRadius: 3,
    marginVertical: 3,
    marginHorizontal: "3%",
  },
  textInput: {
    fontSize: 12,
    textAlign: 'left',
    margin: 10,
    color: '#999',
    width: '300px'
  },
};

function mapStateToProps(state) {
  return {
    auth: state.get('auth')
  };
}

export default compose(
  withStyles(mui_styles),
  connect(mapStateToProps)
)(Me);



