import React from 'react';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import { Link } from "react-router-dom";

import { compose } from "redux";
import { connect } from "react-redux";

import { postRequest } from '../lib/requests';

const qs = require('query-string');

const mui_styles = theme => ({
  button: {
    margin: theme.spacing.unit,
  },
  input: {
    display: 'none',
  },
});

class PasswordReset extends React.Component {

  state = {
    loading: false,
    error: false,
    done: false,
    password: 'Androllo6502$',
  };

  onChange = (event) => {
    const { target: { name, value } } = event;
    this.setState(() => ({ [name]: value }))
    console.log([name], value);
  }

  canLogIn = () => {
    const { password } = this.state;
    return !(password);
  };

  onLogIn = async (email, uuid) => {
    console.log("onLogin");
    this.setState({loading: true, error: false});
    try {
      const { password } = this.state;
      const response = await postRequest('/password-set', {email: email, uuid: uuid, password: password});
      console.log(response);
      this.setState({error: response.error, errorMessage: response.message, loading: false});
      if (!this.state.error) {
        this.setState( {done: true} )
      }
      else {
        console.log("Received error");
      }
    } catch (error) {
      this.setState({loading: false, error: true});
    }
  };

  render() {
    const params = qs.parse(this.props.location.search);
    const uuid = params.uuid;
    const email = params.email;

    const { error, errorMessage, done } = this.state;
    const { classes } = this.props;
    if (done) {
      return(
        <div>
          Password updated!
        </div>
      );
    }
    else
    
    return (
      <div style={styles.container}>
        <h2>Set password</h2>
        <div>
          <p style={styles.textInput}>
            {email}
          </p>
        </div>
        <div>
          <p style={styles.textInput}>
            {uuid}
          </p>
        </div>
        <div>
          <TextField
            style={styles.textInput}
            placeholder={'Password'}
            type={'password'}
            onChange={this.onChange}
            value={this.state.password}
            name={'password'}
          />
        </div>
        <div>
          <Button
            variant="raised" color="primary" className={classes.button}
            disabled={this.canLogIn() || this.state.loading}
            onClick={ () => this.onLogIn(email, uuid)}
          >
            Submit
          </Button>
        </div>
        <div>
          {error && <p style={{textAlign: 'left', color: "#ff0000"}}>{errorMessage}</p>}
        </div>
      </div>
    );
  }
}

const styles = {
  container: {
    flex: 1,
    justifyContent: 'center'
  },
  textInputView: {
    borderWidth: 1,
    borderColor: '#efefef',
    borderRadius: 3,
    marginVertical: 3,
    marginHorizontal: "3%",
  },
  textInput: {
    fontSize: 12,
    textAlign: 'left',
    margin: 10,
    color: '#999',
    width: '300px'
  },
};

function mapStateToProps(state) {
  return {
    auth: state.get('auth')
  };
}

export default compose(
  withStyles(mui_styles),
  connect(mapStateToProps)
)(PasswordReset);



