import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';

class TextSearch extends Component {
  constructor(props) {
    super(props);
    this.state = {text: ""};
  }

  handleChange = (event) => {
    const text = event.target.value;
    this.setState({
      text: text,
    });
    this.props.handleSearch(text);
    this.props.handleSearched(text);
  };
  
  render() {
    return(
      <div style={{width: "80%"}}>
        <div style={{width: "100%"}}>
          <TextField
            style = {{width: "100%"}}
            label="Enter label to search"
            value={this.state.text}
            onChange={this.handleChange}
            onBlur={this.props.handleSearched ? () => this.props.handleSearched(this.state.text) : null}
            tabIndex={-1}
          />
        </div>
      </div>
    )
  }
}

export default TextSearch



