import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import { compose } from "redux";
import { connect } from "react-redux";
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import CircularProgress from '@material-ui/core/CircularProgress';

import { getRequest } from '../lib/requests';

const mui_styles = theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'auto',
  },
  button: {
    margin: theme.spacing.unit,
  },
  input: {
    display: 'none',
  },
  table: {
    // minWidth: 700,
  },
});

class MyColleagues extends React.Component {

  state = {
    loading: false,
    loaded: false,
    error: false,
    myColleagues: []
  };

  async load(props) {
    if (!props.auth.token) {
      console.log("No token provided to me");
      return
    }
    else {
      this.setState({
        loading: true
      });
      const response = await getRequest('/users/top-coins', props.auth.token);
      console.log(response);
      this.setState({
        loaded: true,
        myColleagues: response.Items,
      })
    }
  }

  async componentWillMount() {
    await this.load(this.props);
  }

  async componentWillReceiveProps(props) {
    await this.load(props);
  }

  render() {
    const { error, errorMessage } = this.state;
    const { classes } = this.props;

    if (!this.state.loading && !this.state.loaded) {
      return (
        <div style={styles.container}>
          <h2>My Colleagues</h2>
          <p>Not loged in</p>
        </div>
      )
    }

    if (this.state.loading && !this.state.loaded) {
      return (
        <div style={styles.container}>
          <h2>My Colleagues</h2>
          <CircularProgress className={classes.progress} />
        </div>
      )
    }

    return (
      <div style={styles.container}>
        <h2>My Colleagues</h2>
        <Paper className={classes.root}>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell><p align="right">Coins</p></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            { this.state.myColleagues.map( user => {
              return(
                <TableRow key={user.HashKey}>
                  <TableCell>{user.HashKey}</TableCell>
                  {/* <TableCell align="right">{user.coins ? user.coins : 0}</TableCell> */}
                  <TableCell><p align="right">{user.coins ? user.coins : 0}</p></TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
        </Paper>
        <div>
          {error && <p style={{textAlign: 'left', color: "#ff0000"}}>{errorMessage}</p>}
        </div>
      </div>
    );
  }
}

const styles = {
  container: {
    flex: 1,
    justifyContent: 'center'
  },
  textInput: {
    fontSize: 12,
    margin: 10,
    color: '#999',
    width: '300px'
  },
};

function mapStateToProps(state) {
  return {
    auth: state.get('auth')
  };
}

export default compose(
  withStyles(mui_styles),
  connect(mapStateToProps)
)(MyColleagues);



