import React, { Component } from 'react';
import './App.css';
import Header from './components/Header';
import Login from './components/Login';
import New from './components/New';
import About from './components/About';
import PasswordReset from './components/PasswordReset';
import Me from './components/Me';
import MyColleagues from './components/MyColleagues';

import { compose } from "redux";
import { connect } from "react-redux";
import { setAuth } from "./actions/auth";

import { BrowserRouter as Router, Route, Link } from "react-router-dom";

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      token: false
    };
  }
  
  componentDidMount(){
  }

  render() {
    const { auth } = this.props;
    console.log(auth);
    return (
      <div className="App">
        <div className="header clearfix">
          <Header />
        </div>
        <Router>
          <div className="clearfix">
            <div className="column menu underlay" style={{marginTop: 60, backgroundColor: 'green'}}>
              <ul>
                <li><Link to="/">Login</Link></li>
                <li><Link to="/me">My profile</Link></li>
                <li><Link to="/top-coins">My colleagues</Link></li>
                <li><Link to="/about">About</Link></li>
              </ul>
            </div>
            <div className="column content" style={{marginTop: 60}}>
              <Route exact path="/" component={Login} />
              <Route path="/about" component={About} />
              <Route path="/new" component={New} />
              <Route path="/me" component={Me} />
              <Route path="/top-coins" component={MyColleagues} />
              <Route path="/password-reset" component={PasswordReset} />
            </div>
          </div>
        </Router>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    auth: state.get('auth')
  };
}

const actions = {
  setAuth
};

export default compose(connect(mapStateToProps, actions))(App);



